.quiz-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  text-align: center;
}

.question-container {
  margin: 20px 0;
}

.options-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
  margin: 20px 0;
}

.option-button {
  padding: 15px;
  font-size: 16px;
  border: 2px solid #ccc;
  border-radius: 8px;
  background-color: #e0e0e0;
  color: #111;
  cursor: pointer;
  transition: all 0.3s ease;
}

.option-button:hover {
  background-color: #f0f0f0;
}

.option-button:disabled {
  cursor: not-allowed;
}

.option-button.correct {
  background-color: #4caf50;
  color: white;
  border-color: #4caf50;
}

.option-button.incorrect {
  background-color: #f44336;
  color: white;
  border-color: #f44336;
}

.feedback {
  margin: 20px 0;
  font-size: 18px;
  font-weight: bold;
}

button {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #2196f3;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #1976d2;
} 